import { createReducer, types } from 'shared/lib/redux-utils';
import {
  DESELECT_ALL,
  LOAD_DB_EDITION,
  LOAD_DB_VERSION,
  LOAD_LICENSE_DELTA,
  LOAD_NETWORK_EDGES,
  LOAD_NETWORK_NODES,
  LOAD_OS_TYPE,
  LOAD_SERVER_DETAILS,
  LOAD_USAGE,
  PROVIDE_EXECUTIVE_DASHBOARD,
  PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD,
  SELECT_ALL,
  TOGGLE_SERVER
} from '../actions';
import { mapAllServers, mapVirtualServers } from '../mappers';

export const initialState = {
  rawEntries: [],
  allServers: [
    {
      name: '',
      nrCores: 0,
      collDate: null,
      totalDBs: 0,
      eelic_count: 0,
      selic_count: 0,
      sids: [],
      installedOptions: {},
      totalInstalledOptions: 0
    }
  ],
  selectedServers: [
    {
      name: '',
      nrCores: 0,
      collDate: null,
      totalDBs: 0,
      eelic_count: 0,
      selic_count: 0,
      sids: [],
      installedOptions: {},
      totalInstalledOptions: 0
    }
  ],
  rawVirtualServers: [],
  virtualServers: [],
  flags: {
    dashboard: { loading: false, callFailed: false },
    virtual: { loading: false },
    network: {
      nodesLoading: false,
      edgesLoading: false,
      serverDetailsLoading: false,
    },
    techDetails: {
      osTypeLoading: false,
      dbEditionLoading: false,
      dbVersionLoading: false,
    },
    licensableFeatures: {
      loading: false,
    },
    licenseDelta: {
      loading: false,
    }
  },
  networkNodes: [],
  networkEdges: [],
  networkServerDetails: {},
  techDetails: {
    osType: [],
    dbEdition: [],
    dbVersion: [],
  },
  licensableFeatures: [],
  licenseDelta: [],
};

export const producers = {
  [types.loading(PROVIDE_EXECUTIVE_DASHBOARD)]: draft => {
    draft.flags.dashboard.loading = true;
  },
  [types.success(PROVIDE_EXECUTIVE_DASHBOARD)]: (draft, { payload }) => {
    draft.rawEntries = payload.execdashboard;
    draft.allServers = mapAllServers(payload.execdashboard);
    draft.selectedServers = mapAllServers(payload.execdashboard);
    draft.flags.dashboard.loading = false;
    draft.flags.dashboard.callFailed = false;
  },
  [types.fail(PROVIDE_EXECUTIVE_DASHBOARD)]: draft => {
    draft.flags.dashboard.loading = false;
    draft.flags.dashboard.callFailed = true;
  },
  [DESELECT_ALL]: draft => {
    draft.selectedServers = [];
  },
  [SELECT_ALL]: (draft, { payload }) => {
    draft.selectedServers = mapAllServers(payload);
  },
  [TOGGLE_SERVER]: (draft, { payload }) => {
    draft.selectedServers = payload;
  },
  [types.loading(PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD)]: draft => {
    draft.flags.virtual.loading = true;
  },
  [types.success(PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD)]: (draft, { payload }) => {
    draft.rawVirtualServers = payload?.execdashboard_virtual;
    draft.virtualServers = mapVirtualServers(payload?.execdashboard_virtual);
    draft.flags.virtual.loading = false;
    draft.flags.dashboard.callFailed = false;
  },
  [types.fail(PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD)]: draft => {
    draft.flags.virtual.loading = false;
    draft.flags.dashboard.callFailed = true;
  },
  [types.loading(LOAD_NETWORK_NODES)]: draft => {
    draft.flags.network.nodesLoading = true;
  },
  [types.success(LOAD_NETWORK_NODES)]: (draft, { payload }) => {
    draft.networkNodes = payload;
    draft.flags.network.nodesLoading = false;
  },
  [types.fail(LOAD_NETWORK_NODES)]: draft => {
    draft.flags.network.nodesLoading = false;
    draft.networkNodes = [];
  },
  [types.loading(LOAD_NETWORK_EDGES)]: draft => {
    draft.flags.network.edgesLoading = true;
  },
  [types.success(LOAD_NETWORK_EDGES)]: (draft, { payload }) => {
    draft.networkEdges = payload;
    draft.flags.network.edgesLoading = false;
  },
  [types.fail(LOAD_NETWORK_EDGES)]: draft => {
    draft.flags.network.edgesLoading = false;
    draft.networkEdges = [];
  },
  [types.loading(LOAD_SERVER_DETAILS)]: draft => {
    draft.flags.network.serverDetailsLoading = true;
  },
  [types.success(LOAD_SERVER_DETAILS)]: (draft, { payload }) => {
    draft.networkServerDetails = payload;
    draft.flags.network.serverDetailsLoading = false;
  },
  [types.fail(LOAD_SERVER_DETAILS)]: draft => {
    draft.flags.network.serverDetailsLoading = false;
    draft.networkServerDetails = {};
  },
  [types.loading(LOAD_OS_TYPE)]: draft => {
    draft.flags.network.osTypeLoading = true;
  },
  [types.success(LOAD_OS_TYPE)]: (draft, { payload }) => {
    draft.techDetails.osType = payload;
    draft.flags.techDetails.osTypeLoading = false;
  },
  [types.fail(LOAD_OS_TYPE)]: draft => {
    draft.flags.network.osTypeLoading = false;
    draft.techDetails.osType = [];
  },
  [types.loading(LOAD_DB_EDITION)]: draft => {
    draft.flags.network.dbEditionLoading = true;
  },
  [types.success(LOAD_DB_EDITION)]: (draft, { payload }) => {
    draft.techDetails.dbEdition = payload;
    draft.flags.techDetails.dbEditionLoading = false;
  },
  [types.fail(LOAD_DB_EDITION)]: draft => {
    draft.flags.network.dbEditionLoading = false;
    draft.techDetails.dbEdition = [];
  },
  [types.loading(LOAD_DB_VERSION)]: draft => {
    draft.flags.network.dbVersionLoading = true;
  },
  [types.success(LOAD_DB_VERSION)]: (draft, { payload }) => {
    draft.techDetails.dbVersion = payload;
    draft.flags.techDetails.dbVersionLoading = false;
  },
  [types.fail(LOAD_DB_VERSION)]: draft => {
    draft.flags.network.dbVersionLoading = false;
    draft.techDetails.dbVersion = [];
  },
  [types.loading(LOAD_USAGE)]: draft => {
    draft.flags.licensableFeatures.loading = true;
  },
  [types.success(LOAD_USAGE)]: (draft, { payload }) => {
    draft.licensableFeatures = payload;
    draft.flags.licensableFeatures.loading = false;
  },
  [types.fail(LOAD_USAGE)]: draft => {
    draft.flags.licensableFeatures.loading = false;
    draft.licensableFeatures = [];
  },
  [types.loading(LOAD_LICENSE_DELTA)]: draft => {
    draft.flags.licenseDelta.loading = true;
  },
  [types.success(LOAD_LICENSE_DELTA)]: (draft, { payload }) => {
    draft.licenseDelta = payload;
    draft.flags.licenseDelta.loading = false;
  },
  [types.fail(LOAD_LICENSE_DELTA)]: draft => {
    draft.flags.licenseDelta.loading = false;
    draft.licenseDelta = [];
  },
};

export default createReducer(producers, initialState);
