export function loadNetworkNodesThunk(serverId) {
  return async dispatch => {
    dispatch(creators.loading(LOAD_NETWORK_NODES));

    try {
      let url = `${CONSTANTS.BASE.ENDPOINTS.EXECUTIVE_DASHBOARD}/network/nodes`;
      if (serverId) {
        url = `${url}/dd?s_id=${serverId}`;
      }
      const response = await RestClient.get(url);
      dispatch(creators.success(LOAD_NETWORK_NODES, response.data.data || []));
    } catch (error) {
      dispatch(creators.fail(LOAD_NETWORK_NODES, error));
    }
  };
}

