import React, { PureComponent } from 'react';
import Graph from 'react-graph-vis';
import {
  BASE_NETWORK_GRAPH_OPTIONS,
  DB_NODE_COLOR,
  DB_NODE_SHAPE,
  NODE_SIZE_MULTIPLIER,
  ROOT_NODE,
  ROOT_NODE_ID,
  SERVER_NODE_COLOR,
  SERVER_NODE_SHAPE,
  ServerDetails
} from './ServersNetworkConstants';
import 'vis-network/styles/vis-network.css';
import './ServersNetworkTile.local.scss';
import { connect } from 'react-redux';
import { loadNetworkEdgesThunk, loadNetworkNodesThunk, loadServerDetailsThunk } from '../../store/thunks';
import { Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';

class ServersNetworkTile extends PureComponent {
  events = {
    select: (event) => {
      const { nodes } = event;
      if (nodes.length) {
        const nodeId = nodes[0];
        const selectedNode = this.networkData.graph.nodes.find((node) => node.id === nodeId);
        if (selectedNode && selectedNode.isServerNode) {
          const { onServerChanged, getServerDetails } = this.props;
          onServerChanged(selectedNode.id);
          getServerDetails(selectedNode);
        }
      }
    }
  };

  componentDidMount() {
    const { getNetworkNodes, getNetworkEdges } = this.props;
    getNetworkNodes();
    getNetworkEdges();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getNetworkNodes, getNetworkEdges } = this.props;
      setTimeout(() => {
        getNetworkNodes(newProps.selectedServer);
        getNetworkEdges(newProps.selectedServer);
      });
    }
  }

  render() {
    const { nodes, edges, selectedServer, isLoading } = this.props;
    const preparedNodes = this.mapNodes(nodes);
    const serverNodes = preparedNodes.filter((node) => node.isServerNode);
    if (preparedNodes.length && !selectedServer) {
      preparedNodes.unshift(ROOT_NODE);
    }
    this.networkData = {
      graph: {
        nodes: preparedNodes,
        edges: this.mapEdges(edges, serverNodes),
      }
    };
    const serverDetails = ServerDetails;
    if (isLoading) {
      return (
        <>
          <div className="exec-dashboard-graph-container servers-network-graph-container">
            <Center>
              <Loader>Loading Network Data...</Loader>
            </Center>
          </div>
        </>
      );
    }

    const hasData = preparedNodes.length && edges.length;
    return (
      <div className="exec-dashboard-graph-container servers-network-graph-container">
        {hasData ? (
          <>
            <Graph
              graph={this.networkData.graph}
              options={BASE_NETWORK_GRAPH_OPTIONS}
              events={this.events}
            />
            {selectedServer && (
              <div className="server-details-container">
                {serverDetails.map((row, index) => (
                  <div className="server-details-row" key={`${row.name}_${index}`}>{row.name}: {row.value}</div>
                ))}
              </div>
            )}
          </>
        ) : (
          <Center>
            No Network Data Available
          </Center>
        )}
      </div>
    );
  }

  mapNodes = (nodes) => {
    return (nodes || [])
      .filter((node, index, arr) => {
        return arr.findIndex(t => (t.id === node.id)) === index;
      })
      .map((node) => {
        const isServerNode = node.title === 'server';
        return {
          ...node,
          isServerNode,
          title: node.label,
          widthConstraint: node.size * NODE_SIZE_MULTIPLIER,
          color: isServerNode ? SERVER_NODE_COLOR : DB_NODE_COLOR,
          shape: isServerNode ? SERVER_NODE_SHAPE : DB_NODE_SHAPE
        };
      });
  }

  mapEdges = (edges, serverNodes) => {
    const serverEdges = serverNodes.map((node) => {
      return { from: ROOT_NODE_ID, to: node.id, width: 5, arrows: { to: { enabled: false } } };
    });
    return edges.map((edge) => {
      return { from: edge.from, to: edge.to, width: 5, arrows: { to: { enabled: false } } };
    }).concat(serverEdges);
  }
}

export const ConnectedServersNetworkTile = connect(
  state => {
    return {
      isLoading:
        state.executiveDashboard.flags.network.nodesLoading ||
        state.executiveDashboard.flags.network.edgesLoading,
      nodes: state.executiveDashboard.networkNodes,
      edges: state.executiveDashboard.networkEdges,
    };
  },
  dispatch => ({
    getNetworkNodes: (serverId) => dispatch(loadNetworkNodesThunk(serverId)),
    getNetworkEdges: (serverId) => dispatch(loadNetworkEdgesThunk(serverId)),
    getServerDetails: (serverId) => dispatch(loadServerDetailsThunk(serverId)),
  })
)(ServersNetworkTile);
