import React, { PureComponent } from 'react';
import { Bar, BarChart, CartesianGrid, Cell, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import './LicenseDeltaTile.local.scss';
import { ChartTooltip } from '../ChartTooltip/ChartTooltip';
import { connect } from 'react-redux';
import { loadLicenseDeltaThunk } from '../../store/thunks';
import {
  LICENSABLE_FEATURE_DEFAULT_COLOR,
  LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
  LICENSABLE_FEATURES_COLORS
} from '../LicensableFeaturesGraph/LicensableFeaturesConstants';

class LicenseDeltaTile extends PureComponent {
  componentDidMount() {
    const { getLicenseDelta } = this.props;
    getLicenseDelta();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getLicenseDelta } = this.props;
      setTimeout(() => getLicenseDelta(newProps.selectedServer));
    }
  }

  render() {
    const { licenseDelta, selectedServer } = this.props;
    const chartData = this.mapData(licenseDelta);
    return (
      <div className="exec-dashboard-graph-container license-delta-graph-container">
        <div className="chart-title">License Delta</div>
        <ResponsiveContainer height={380}>
          <BarChart data={chartData} barGap={0} margin={{ left: 20 }}>
            <CartesianGrid strokeDasharray="3 3" vertical={false} />
            <XAxis dataKey="label" tickSize={false} axisLine={false} tick={this.renderCustomXAxisTick} />
            <YAxis
              width={20}
              tickSize={false}
              allowDecimals={false}
              type="number"
              domain={[0, selectedServer ? 2 : 'dataMax']}
              stroke="rgba(4, 17, 37, 0.87)"
              label={{ value: 'Count of Feature in envt', angle: -90, position: 'left' }}
            />
            <Bar dataKey="value">
              {chartData.map((entry, index) => {
                return <Cell key={`${entry.name}-${index}`} fill="#13369f" />;
              })}
            </Bar>
            <Bar dataKey="used">
              {chartData.map((entry, index) => {
                return <Cell key={`${entry.name}-${index}`} fill="#f2c94c" />;
              })}
            </Bar>
            <Tooltip content={ChartTooltip} cursor={false} />
          </BarChart>
        </ResponsiveContainer>
      </div>
    );
  }

  renderCustomXAxisTick = ({ x, y, payload }) => {
    const licenseConfig = LICENSABLE_FEATURES_COLORS.find((config) => config.type === payload.value);
    return (
      <g transform={`translate(${x},${y})`}>
        <text className="x-axis-label" x={0} y={0} dy={20} textAnchor="middle" fill={licenseConfig?.color || '#aaa'}>
          {payload.value}
        </text>
      </g>
    );
  };

  mapData = (licenseData) => {
    const groupedData = (licenseData || [])
      .reduce((acc, license) => {
        if (acc[license.product_id]) {
          const updated = { ...acc[license.product_id] };
          updated.lic_owned += license.lic_owned;
          updated.lic_used += license.lic_used;
          acc[license.product_id] = updated;
        } else {
          acc[license.product_id] = license;
        }
        return acc;
      }, {});

    return Object.values(groupedData)
      .map((license) => {
        const licenseConfig = LICENSABLE_FEATURES_COLORS.find((config) => license.abbreviation === config.type);
        return {
          name: license.product_name,
          label: license.abbreviation,
          value: license.lic_owned,
          used: license.lic_used,
          color: licenseConfig ? licenseConfig.color : LICENSABLE_FEATURE_DEFAULT_COLOR,
          secondaryColor: licenseConfig ? licenseConfig.secondaryColor : LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
        };
      });
  };
}

export const ConnectedLicenseDeltaTile = connect(
  state => {
    return {
      isLoading: state.executiveDashboard.flags.licenseDelta.loading,
      licenseDelta: state.executiveDashboard.licenseDelta,
    };
  },
  dispatch => ({
    getLicenseDelta: (serverId) => dispatch(loadLicenseDeltaThunk(serverId)),
  })
)(LicenseDeltaTile);
