import React, { PureComponent } from 'react';
import { Cell, Pie, PieChart, ResponsiveContainer, Tooltip } from 'recharts';
import {
  LICENSABLE_FEATURE_DEFAULT_COLOR,
  LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
  LICENSABLE_FEATURES_COLORS,
  LicensableFeaturesSpec,
  LicensableFeaturesSpecForSelectedServer
} from './LicensableFeaturesConstants';
import { OptionsList } from '../OptionsTile/components/OptionsList/OptionsList';
import { Overflow } from '../../../shared/components/layout';
import classnames from 'classnames';
import { ChartTooltip } from '../ChartTooltip/ChartTooltip';
import './LicensableFeaturesTile.local.scss';
import { connect } from 'react-redux';
import { loadUsageThunk } from '../../store/thunks';

const ScoreItem = ({ clazz, label, value }) => {
  return (
    <div className={classnames('score-item-container', clazz)}>
      <div className="score-item-title">{label}</div>
      <div className="score-item-value">{value}</div>
    </div>
  );
};

class LicensableFeaturesTile extends PureComponent {
  componentDidMount() {
    const { getLicensableFeatures } = this.props;
    getLicensableFeatures();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getLicensableFeatures } = this.props;
      setTimeout(() => getLicensableFeatures(newProps.selectedServer));
    }
  }

  render() {
    const { selectedServer, licensableFeatures } = this.props;
    const featuresSpec = selectedServer ? LicensableFeaturesSpecForSelectedServer : LicensableFeaturesSpec;
    const licensesData = this.mapLicenses(licensableFeatures);
    const currencyFormatter = new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      maximumFractionDigits: 0,
    });

    return (
      <div className="exec-dashboard-graph-container licensable-features-graph-container">
        <ScoreItem clazz="total-risk" label="Total Risk" value={currencyFormatter.format(featuresSpec.totalRisk)} />
        <ScoreItem clazz="license-cost" label="License Cost" value={currencyFormatter.format(featuresSpec.licenseCost)} />
        <ScoreItem clazz="support-cost" label="Support Cost" value={currencyFormatter.format(featuresSpec.supportCost)} />
        <ScoreItem clazz="unallocated-licenses" label="Unallocated Licenses" value={featuresSpec.unallocatedLicenses} />
        <ScoreItem clazz="overages" label="Overages" value={featuresSpec.overages} />

        <ResponsiveContainer height={420}>
          <PieChart>
            <Pie data={licensesData} dataKey="value" cx="40%" cy="60%" innerRadius={65} outerRadius={130} blendStroke>
              {licensesData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={entry.color} />
              ))}
            </Pie>
            <Tooltip content={ChartTooltip} />
          </PieChart>
        </ResponsiveContainer>

        <div className="graph-legend-container">
          <Overflow>
            <OptionsList options={licensesData} />
          </Overflow>
        </div>
      </div>
    );
  }

  mapLicenses = (licenses) => {
    return (licenses || []).map((license) => {
      const licenseConfig = LICENSABLE_FEATURES_COLORS.find((config) => license.abbreviation === config.type);
      return {
        ...license,
        name: license.product_name,
        label: license.abbreviation,
        color: licenseConfig ? licenseConfig.color : LICENSABLE_FEATURE_DEFAULT_COLOR,
        secondaryColor: licenseConfig ? licenseConfig.secondaryColor : LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
      };
    });
  }
}

export const ConnectedLicensableFeaturesTile = connect(
  state => {
    return {
      isLoading: state.executiveDashboard.flags.licensableFeatures.loading,
      licensableFeatures: state.executiveDashboard.licensableFeatures,
    };
  },
  dispatch => ({
    getLicensableFeatures: (serverId) => dispatch(loadUsageThunk(serverId)),
  })
)(LicensableFeaturesTile);
