import React, { PureComponent } from 'react';
import { Cell, Pie, PieChart, ResponsiveContainer, Tooltip } from 'recharts';
import './OperatingSystemsTile.local.scss';
import {
  EE_DB_PRIMARY_COLOR,
  EE_DB_SECONDARY_COLOR,
  LINUX_OS_PRIMARY_COLOR,
  LINUX_OS_SECONDARY_COLOR,
  OperatingSystemsConstants,
  OperatingSystemsForSelectedServer,
  SE_DB_PRIMARY_COLOR,
  SE_DB_SECONDARY_COLOR,
  WINDOWS_OS_PRIMARY_COLOR,
  WINDOWS_OS_SECONDARY_COLOR
} from './OperatingSystemsConstants';
import { Overflow } from 'shared/components/layout';
import { OptionsList } from '../OptionsTile/components/OptionsList/OptionsList';
import { ChartTooltip } from '../ChartTooltip/ChartTooltip';
import { connect } from 'react-redux';
import { loadDBEditionThunk, loadDBVersionThunk, loadOSTypeThunk } from '../../store/thunks';
import { Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';

class OperatingSystemsTile extends PureComponent {
  componentDidMount() {
    const { getOSType, getDBEdition, getDBVersion } = this.props;
    getOSType();
    getDBEdition();
    getDBVersion();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getOSType, getDBEdition, getDBVersion } = this.props;
      setTimeout(() => {
        getOSType(newProps.selectedServer);
        getDBEdition(newProps.selectedServer);
        getDBVersion(newProps.selectedServer);
      });
    }
  }

  render() {
    const { isLoading, selectedServer, graphData } = this.props;
    const serverVersionsPieData = selectedServer ? OperatingSystemsForSelectedServer.serverVersionsData : OperatingSystemsConstants.serverVersionsData;

    if (isLoading) {
      return (
        <>
          <div className="exec-dashboard-graph-container operating-systems-graph-container">
            <Center>
              <Loader>Loading Data...</Loader>
            </Center>
          </div>
        </>
      );
    }

    const preparedOSData = this.mapOS(graphData?.osType);
    const preparedDBEditionsData = selectedServer ? this.mapDBEditionByServer(graphData?.dbEdition) : this.mapDBEdition(graphData?.dbEdition);
    const hasData = graphData && (preparedOSData.length || preparedDBEditionsData.length || graphData.dbVersion.length);

    return (
      <div className="exec-dashboard-graph-container operating-systems-graph-container">
        {hasData ? (
          <div className="charts-container">
            <div className="chart">
              <div className="chart-title">Operating System</div>
              <div className="graph-legend-container">
                <Overflow>
                  <OptionsList options={preparedOSData} />
                </Overflow>
              </div>
              <ResponsiveContainer height={380}>
                <PieChart>
                  <Pie data={preparedOSData} dataKey="value" cx="50%" cy="60%" innerRadius={40} outerRadius={80} blendStroke>
                    {preparedOSData.map((entry, index) => (
                      <Cell key={`cell-${entry.name}-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip content={ChartTooltip} />
                </PieChart>
              </ResponsiveContainer>
            </div>
            <div className="chart">
              <div className="chart-title">DB Edition</div>
              <div className="graph-legend-container">
                <Overflow>
                  <OptionsList options={preparedDBEditionsData} />
                </Overflow>
              </div>
              <ResponsiveContainer height={380}>
                <PieChart>
                  <Pie data={preparedDBEditionsData} dataKey="value" cx="50%" cy="60%" innerRadius={40} outerRadius={80} blendStroke>
                    {preparedDBEditionsData.map((entry, index) => (
                      <Cell key={`cell-${entry.database_edition}-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip content={ChartTooltip} />
                </PieChart>
              </ResponsiveContainer>
            </div>
            <div className="chart">
              <div className="chart-title">DB Version</div>
              <div className="graph-legend-container">
                <Overflow>
                  <OptionsList options={serverVersionsPieData} />
                </Overflow>
              </div>
              <ResponsiveContainer height={380}>
                <PieChart>
                  <Pie data={serverVersionsPieData} dataKey="value" cx="50%" cy="60%" innerRadius={40} outerRadius={80} blendStroke>
                    {serverVersionsPieData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip content={ChartTooltip} />
                </PieChart>
              </ResponsiveContainer>
            </div>
          </div>
        ) : (
          <Center>
            No Data Available
          </Center>
        )}
      </div>
    );
  }

  mapOS = (osData) => {
    return (osData || []).map((os) => {
      const isWindowsOS = os.name.indexOf('Windows') !== -1;
      return {
        ...os,
        isWindowsOS,
        label: os.name,
        color: isWindowsOS ? WINDOWS_OS_PRIMARY_COLOR : LINUX_OS_PRIMARY_COLOR,
        secondaryColor: isWindowsOS ? WINDOWS_OS_SECONDARY_COLOR : LINUX_OS_SECONDARY_COLOR,
      };
    });
  }

  mapDBEdition = (dbEditionData) => {
    return (dbEditionData || []).map((dbEdition) => {
      const isSEEdition = dbEdition.name.indexOf('SE') !== -1;
      return {
        ...dbEdition,
        isSEEdition,
        label: dbEdition.name,
        color: isSEEdition ? SE_DB_PRIMARY_COLOR : EE_DB_PRIMARY_COLOR,
        secondaryColor: isSEEdition ? SE_DB_SECONDARY_COLOR : EE_DB_SECONDARY_COLOR,
      };
    });
  }

  mapDBEditionByServer = (dbEditionData) => {
    return (dbEditionData || []).map((dbEdition) => {
      const isSEEdition = (dbEdition.database_edition || dbEdition.name).indexOf('SE') !== -1;
      return {
        ...dbEdition,
        isSEEdition,
        name: dbEdition.database_edition || dbEdition.name,
        value: dbEdition.count || dbEdition.value,
        label: dbEdition.database_edition || dbEdition.name,
        color: isSEEdition ? SE_DB_PRIMARY_COLOR : EE_DB_PRIMARY_COLOR,
        secondaryColor: isSEEdition ? SE_DB_SECONDARY_COLOR : EE_DB_SECONDARY_COLOR,
      };
    });
  }
}

export const ConnectedOperatingSystemsTile = connect(
  state => {
    return {
      isLoading:
        state.executiveDashboard.flags.network.nodesLoading ||
        state.executiveDashboard.flags.network.edgesLoading,
      graphData: state.executiveDashboard.techDetails,
    };
  },
  dispatch => ({
    getOSType: (serverId) => dispatch(loadOSTypeThunk(serverId)),
    getDBEdition: (serverId) => dispatch(loadDBEditionThunk(serverId)),
    getDBVersion: (serverId) => dispatch(loadDBVersionThunk(serverId)),
  })
)(OperatingSystemsTile);
